<template>

    <div class="login-container">
      <div class="login-box">
          <div class="avator-box">
              <img src="../assets/logo.png" alt="">
          </div>

    
       <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
      <el-form-item prop="username">
         <el-input prefix-icon="el-icon-user-solid" v-model="ruleForm.username"></el-input>
     </el-form-item>
      <el-form-item prop="password">
         <el-input prefix-icon="el-icon-lock" v-model="ruleForm.password"></el-input>
     </el-form-item>


       <el-form-item class="btn-box">
         <el-button type="primary" @click="add()">登录</el-button>
         <el-button type="info">重置</el-button>
       </el-form-item>
    
      </el-form>

     
      </div> 
    </div>
    
  
</template>

<script>
export default {
  data() {
    return {
       ruleForm: {
          username:'admin',
          password:'123456'
        },
        rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
          ],
           password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
          ],
        },
        
    };
  },
  created() {},
  mounted() {},
  methods: {
    add() {
      //   获取接口
      this.$http
        .post("/login", this.ruleForm).then(res => {
          console.log(res);
          if(res.data.meta.status !=200)  return false
              window.sessionStorage.setItem("token",res.data.data.token)
              this.$router.push('/list')
          // alert("登陆成功！")
          
        });
    },
  }
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
}
.login-container{
    width: 450px;
    height: 304px;
    border-radius: 3px;
    top: 50%;
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
}
.avator-box{
    width: 130px;
    height: 130px;
    padding: 10px;
    border: 1px solid #efefef;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
    img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #efefef;
    }
}

.login-box{
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 10px #ddd;
    width: 450px;
    height: 304px;
    border-radius: 3px;
    top: 50%;
}
.el-form{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}
.btn-box{
  display: flex;
    justify-content: flex-end;
}
</style>
