<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/list' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/">用户管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 表格列表 -->
    <el-card class="box-card">
      <!-- 栅格布局 -->
      <el-row :gutter="12">
        <el-col :span="8">
          <!-- 搜索框 -->
          <el-input placeholder="请输入内容" v-model="userAdd.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        </el-col> 
        <el-row>
          <el-col :span="4">
            <el-button type="primary"  @click="dialogFormVisible = true">添加用户</el-button>
          </el-col>
        </el-row>
 <!-- 添加用户弹框 -->
            <el-dialog title="添加用户" :visible.sync="dialogFormVisible">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                  <el-form-item label="用户名" prop="username">
                      <el-input v-model="ruleForm.username"></el-input>
                  </el-form-item>
                  <el-form-item label="密码" prop="password">
                      <el-input v-model="ruleForm.password" type="password"></el-input>
                  </el-form-item>
                  <el-form-item label="邮箱" prop="email">
                      <el-input v-model="ruleForm.email"></el-input>
                  </el-form-item>
                  <el-form-item label="电话" prop="mobile">
                      <el-input v-model="ruleForm.mobile"></el-input>
                  </el-form-item>
                </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitAdd('ruleFrom')">确 定</el-button>
            </div>
            </el-dialog>
      </el-row>

      <!-- 表格展示 -->
      <el-table border :data="codeAdd" style="width: 100%" margin-top:12px>
        <!-- 索引 -->
        <el-table-column type="index" label="#" width="80"></el-table-column>
        <!-- 姓名 -->
        <el-table-column prop="username" label="姓名" width="180"></el-table-column>
        <!-- 邮箱 -->
        <el-table-column prop="email" label="邮箱" width="180"></el-table-column>
        <!-- 电话 -->
        <el-table-column prop="mobile" label="电话" width="180"></el-table-column>
        <!-- 角色 -->
        <el-table-column prop="role_name" label="角色" width="180"></el-table-column>
        <!-- 状态 -->
        <el-table-column prop="mg_state" label="状态" width="180">
            <template slot-scope="scope">
                <!-- {{ scope.row }} -->
                <el-switch
                    v-model="scope.row.mg_state"
                    active-color="#409EFF"
                    inactive-color="#DCDFE6">
                </el-switch>
            </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column prop="do" label="操作" width="180">
          <template slot-scope="">
                <!-- {{ scope.row }} -->
                      <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
                      <el-button size="mini" type="warning" icon="el-icon-star-off"></el-button>
                      <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
            </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->

      <el-pagination
        @size-change="bookSize"
        @current-change="changePage"
        :current-page="userAdd.pagenum"
        :page-sizes="[1, 2, 3, 10]"
        :page-size="userAdd.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      totalNum: 0, //页面总数
      userAdd: {
        query: "",
        pagenum: 1, //用户初始页数
        pagesize: 2 //每页几条
      },
      codeAdd:[],
      dialogFormVisible:false, //添加弹框
       ruleForm: {
          username: '',
          password: '',
          email: '',
          mobile: '',
        },
         rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
           password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 8, max: 16, message: '长度在 8 到 16 个字符', trigger: 'blur' }
          ],
           email: [
            { required: true, message: '请输入邮箱', trigger: 'blur' },
          ],
           mobile: [
            { required: true, message: '请输入手机号', trigger: 'blur' },
          ]
         }
    };
  },
  created() {},
  mounted() {
    this.userFn(); //用户展示
    this.submitAdd();  //新增用户
    
  },
  methods: {
    // 用户列表请求
    userFn() {
      this.$http
        .get(
          `/users?query=${this.userAdd.query}&pagenum=${this.userAdd.pagenum}&pagesize=${this.userAdd.pagesize}`
        )
        .then(res => {
        //   console.log(res);
          this.codeAdd = res.data.data.users;
          this.totalNum = res.data.data.total;
        });
    },
    //   当每页条数发生改变时size变化
    bookSize(size) {
      console.log(size);
      this.userAdd.pagesize = size;
      this.userFn();
    },

    //   当前页发生改变时 出发1，2，3
    changePage(page) {
      console.log(page);
      this.userAdd.pagenum = page;
      this.userFn();
    },

    // 添加用户方法
    submitAdd(ruleFrom){
       console.log(ruleFrom)
    },
  }
};
</script>

<style lang="scss" scoped>
.box-card {
  margin-top: 12px;
}
.el-table {
  margin-top: 12px;
}
.el-pagination {
  margin-top: 12px;
}
</style>
