<template>
    <div>
        <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
         <el-breadcrumb-item :to="{ path: '/list' }">首页</el-breadcrumb-item>
         <el-breadcrumb-item>权限管理</el-breadcrumb-item>
         <el-breadcrumb-item>权限列表</el-breadcrumb-item>
     </el-breadcrumb>

        <!-- 表格列表 -->
    <el-card class="box-card">
      <el-table
      border
      stripe
      :data="tableData"
      style="width: 100%">
      <el-table-column
        type="index"
        label="#"
        width="40">
      </el-table-column>
      <el-table-column
        prop="authName"
        label="权限名称"
        width="300">
      </el-table-column>
      <el-table-column
        prop="path"
        label="路径"
        width="300">
      </el-table-column>
      <el-table-column
        prop="address"
        label="权限等级"
        >
        <el-tag>level</el-tag>
      </el-table-column>
    </el-table>
    </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
        tableData: [],

        };
    },
    created() {

    },
    mounted() {
      this.picks()
    },
    methods: {
       picks(){
            this.$http.get("/rights/list").then((res)=>{
              //  console.log(res)
                this.tableData = res.data.data
                // console.log(this.tableData)
            })
        }
    }
};
</script>

<style scoped>
.box-card {
  margin-top: 12px;
}
.el-table {
  margin-top: 12px;
}
</style>
