<template>
  <div class="big" style="height:100%;">
    <el-container style="height:100%;">
      <!-- 头 -->
      <el-header>
        <div class="header-left">
          <img src="../assets/a.png" style="width=60  height=56" />
          <h3 class="title">电商后台管理系统</h3>
        </div>
        <el-button type="info" @click="out">退出</el-button>
      </el-header>
      <!-- 头 -->

      <el-container>
        <el-aside width="200px">
          <div @click="isCollapse = !isCollapse" class="top-bar">|||</div>
          <el-menu
            router
            :collapse-transition="false"
            :collapse="isCollapse"
            unique-opened
            background-color="#333744"
            text-color="#fff"
            active-text-color="#409EFF"
            :default-active="activePath"
          >
            <!-- 一级菜单 -->
            <el-submenu :index="'/list/'+item.path" v-for="(item) in menu" :key="item.id">
              <template slot="title">
                <i :class="iconObj[item.id]"></i>
                <span slot="title">{{ item.authName }}</span>
              </template>
              <!-- 一级菜单 -->

              <!-- 二级菜单 -->
              <el-menu-item
                @click="savePath()"
                :index="'/list/'+subitem.path"
                v-for="(subitem) in item.children"
                :key="subitem.id"
              >
                <i class="el-icon-menu"></i>
                {{ subitem.authName }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>

        <!-- 中间显示区域 -->
          <el-main>
            <router-view></router-view>
          </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Welcome from "../components/Welcome";


export default {
  data() {
    return {
      isCollapse: false, //默认不折叠
      menu: [],
      iconObj: {
        "125": "el-icon-user-solid",
        "103": "el-icon-s-cooperation",
        "101": "el-icon-s-shop",
        "102": "el-icon-s-claim",
        "145": "el-icon-s-platform"
      },

      activePath: "" //二级路由匹配
    };
  },
  created() {
    let path = window.sessionStorage.getItem("activePath");
    this.activePath = path;
  },
  mounted() {
    this.mun();
    // this.list()
  },
  // 路由监听
  // watch: {
  //   $route(to, from) {
  //     console.log(to, from);
  //     window.sessionStorage.setItem("activePath", to.path);
  //     this.activePath = to.path;
  //   }
  // },
  components: {
    Welcome
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },

    //  退出按钮
    out() {
      window.sessionStorage.removeItem("token");
      this.$router.push("/");
    },

    // 左侧菜单栏接口
    mun() {
      this.$http.get("/menus").then(res => {
        // console.log(res)
        this.menu = res.data.data;
      });
    },

    // 用户点击路由
    savePath(path) {
      //点击存储Path
      window.sessionStorage.setItem("activePath", +path);
      this.activePath = path;
    }
  }
};
</script>

<style lang="scss">
.el-header {
  background-color: #373d41;
  color: #333;
  display: flex;
  justify-content: space-between;
  .el-button {
    float: right;
    text-align: center;
    justify-items: center;
    margin: 12px 20px;
  }
}

.el-aside {
  background-color: #333744;
  color: #333;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
}
body > .el-container {
  margin-bottom: 40px;
  width: 100%;
  height: 100%;
}
.header-left {
  display: flex;
  .title {
    color: white;
    font-size: 20px;
    font-weight: bold;
    margin: 13px 10px;
  }
  .el-menu {
    border: none;
  }
  .el-menu-item {
    font-size: 12px;
  }
}
.el-radio-group {
  height: 40px;
  background-color: #545c64;
}
.el-radio-button {
  background-color: #545c64;
}
.top-bar {
  background-color: #4a5064;
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 22px;
  cursor: pointer;
  letter-spacing: 0.12em;
}
</style>
