<template>
    <div>
    <!-- 面包屑导航 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
         <el-breadcrumb-item :to="{ path: '/list' }">首页</el-breadcrumb-item>
         <el-breadcrumb-item>商品管理</el-breadcrumb-item>
         <el-breadcrumb-item>商品列表</el-breadcrumb-item>
     </el-breadcrumb>

     <el-card class="box-card">

    <!-- 搜索 -->
         <div style="margin-top: 15px;">
            <el-input placeholder="请输入内容">
            <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <el-button type="primary">添加商品</el-button>
    </div>
        <el-table
            :data="goodsList"
            border
            stripe
            style="width: 100%">
           <el-table-column
                type="index"
                label="#"
                width="40">
            </el-table-column>
            <el-table-column
                prop="goods"
                label="商品名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="goods_price"
                label="商品价格（元）"
                width="180">
            </el-table-column>
            <el-table-column
                prop=""
                label="商品重量"
                width="180">
            </el-table-column>
            <el-table-column
                prop=""
                label="创建时间"
                width="180">
            </el-table-column>
            <el-table-column
                prop=""
                label="操作"
                width="180">
            </el-table-column>
        </el-table>

        <!-- 分页器 -->
     
     </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
        totalNum:0,  //页面总数
        tableList:{
                query: "",
                pagenum: 1, //用户初始页数
                pagesize: 2 //每页几条
            },
        goodsList:[]
        };
        
    },
    created() {

    },
    mounted() {
      this.FootGoods();
    },
    methods: {
      FootGoods(){
          this.$http.get(`/goods?query=${this.tableList.query}&pagenum=${this.tableList.pagenum}&pagesize=${this.tableList.pagesize}`
        )
        .then(res => {
          console.log(res);
          this.goodsList = res.data.data.goods.goods_name;
        //   this.totalNum = res.data.data;
        });

   
      }
    },
};
</script>

<style scoped>
.box-card {
  margin-top: 12px;
}
.el-table {
  margin-top: 12px;
}
.el-input {
    width: 420px;
    padding-right: 30px;
  }
</style>
